@import url(https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
.App-bg {
  display: flex;
  min-width: 400px;
  max-width: 500px;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  /* position: fixed; */
  top: 0;
  bottom: 0;
  width: 100%;
  /* min-height: 720px; */
  min-height: 620px;
  margin-left: auto;
  margin-right: auto;
  flex-wrap: nowrap;
}

.main-img {
  z-index: -10;
  width: 400px;
  position: absolute;
}

.main-test {
  display: inline-block;
  margin: 0;
  width: 10ex;
  height: 2.5ex;
  line-height: 2.5ex;
  border-radius: 2ex;
  box-sizing: border-box;
  border-style: solid;
  border-width: 1px;
  border-color: black;
  /* font-size: 25px; */
  font-size: 20px;
  margin-top: 360px;
}

.main-text {
  margin: -1ex;
  /* font-size: 60px; */
  font-size: 40px;
}

.start-btn {
  font-family: 'Nanum Pen Script', cursive;
  transition: all .5s ease-in-out;
  box-shadow: 0 1px 7px rgba(0,0,0,0.25);
  border: 0;
  /* 
  width: 70px;
  height: 70px;
  border-radius: 35px;
  margin-top: 30px; 
  font-size: 38px;
  */
  width: 60px;
  height: 60px;
  border-radius: 30px;
  margin-top: 20px;
  font-size: 32px;
  color: white;
  background-color: black;
  outline: none;
}

.start-btn:hover {
  color: black;
  background-color: white;
}
.question__container {
  /* padding: 50px; */
  padding-left: 50px;
  padding-right: 50px;
  padding-top: 35px;
  padding-bottom: 25px;
  margin: 0;
  /* margin-top: 50px; */
  margin-top: 30px;
  width: 100%;
  min-width: 400px;
  max-width: 500px;
  font-weight: bold;
  font-family: 'Noto Sans KR', sans-serif;
}

.progress-bar__container {
  position: relative;
  margin-bottom: 50px;
}

.progress-bar {
  border-radius: 60px;
  overflow: hidden;
  width: 100%;
}

.bar {
  background: rgba(0, 0, 0, 0.1);
}

.progress {
  transition: width 1.5s;
  background: tomato;
  color: #fff;
  padding: 5px;
  width: 0;
}

.progress-bar span {
  display: block;
}

.progress-bar {
  left: 50%;
  max-width: 90%;
  position: absolute;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.question__q {
  position: relative;
  height: 120px;
  /* margin-top: 100px;
  margin-bottom: 100px; */
  margin-top: 60px;
  margin-bottom: 80px;
  font-size: 25px;
  display: flex;
  flex-direction: column;
  text-align: left;
  line-height: 1.2;
}

.question__btn {
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 14px;
  transition: all .5s ease-in-out;
  box-shadow: 0px 1px 7px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  width: 300px; 
  height: 90px;
  margin-bottom: 20px;
  background-color: rgba(255, 72, 0, 0.25);
  border-color: rgba(255, 72, 0, 0.5);
  border-style: dotted;
  color: darkslategrey;
  outline: 0;
}

.question__btn:hover {
  box-shadow: 0px 1px 7px rgba(0, 0, 0, 0.7);
  color: black;
}
.result__container {
  font-family: 'Noto Sans KR', sans-serif;
}

.loader {
  width: 100%;
  line-height: 700px;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.result {
  background-color: #eff3f7;
  position: relative;
  width: 410px;
  padding: 10px;
}

.result ul {
  -webkit-padding-end: 30px;
          padding-inline-end: 30px;
}

.result_div li {
  text-align: left;
  margin-bottom: 10px;
  line-height: 1.6;
}

.result_div p,li {
  font-size: 16px;
}

.result_div img {
  width: 80%;
  transform: translateX(-8%);
}

.result_div p {
  margin-bottom: 0px;
  color: darkslategrey;
}

.result_desc {
  text-align: left;
  color: darkolivegreen !important;
  padding-left: 15px;
  padding-right: 15px;
  margin-top: 0px;
  line-height: 1.6;
}

.result_div h1 {
  line-height: 1;
  margin: 5px;
  color: tomato;
}

.bible_title {
  margin-top: 50px;
  padding-left: 80px;
  padding-right: 80px;
  color: darkolivegreen !important;
  line-height: 1.4;
}

.bible_content {
  margin-top: 5px;
  font-weight: 800;
  padding-left: 30px;
  padding-right: 30px;
  color: black !important;
}

#link-btn-text {
  font-size: 14px;
  margin-bottom: 15px;
}

#link-btn {
  display: inline-block;
  cursor: pointer;
}

.link-btn-box {
  margin-top: 100px;
  margin-bottom: 40px;
}

.restart_btn {
  font-family: 'Nanum Pen Script', cursive;
  font-size: 30px;
  transition: all .5s ease-in-out;
  border-radius: 35px;
  box-shadow: 0 1px 7px rgba(0,0,0,0.25);
  border: 0;
  width: 180px;
  height: 50px;
  margin-top: 30px;
  color: white;
  background-color: black;
  margin-top: 50px;
  margin-bottom: 100px;
  outline: none;
}

.restart_btn:hover {
  color: black;
  background-color: white;
}
* {
    box-sizing: border-box;
}
  
