    max-width: 500px;
    flex-direction: column;
    align-items: center;
    font-size: calc(10px + 2vmin);
    /* position: fixed; */
    top: 0;
    bottom: 0;
    width: 100%;
    height: 720px;
    margin-left: auto;
    margin-right: auto;
    flex-wrap: nowrap;
}

.kakao_ad_area {
    /* z-index: -1; */
    margin-top: 13px !important;
}
